                </div>
                <div class="flex-body">
                    <ul class="main-ul flex-graph">
                        <li class="main-li">
                            <div class="text"  @click="goPage('/admin/statistics/contentStatistics')">
                                컨텐츠 현황(게시글 현황)  <span>[{{endate}} 기준]</span>
                            </div>
                            <div class="graph">
                                <v-progress-circular v-if="!chartShow"
                                                     :size="100"
                                                     class="progress-box"
                                                     color="#DD1B16"
                                                     indeterminate
                                ></v-progress-circular>
                                <Doughnut v-else
                                          :chart-options="chartOptions"
                                          :chart-data="donutChartData"
                                          :height="height"
                                />
                            </div>
                        </li>
                        <li class="main-li" style="border-right:solid 1px #ddd;margin-right:30px;">
                            <div class="text"   @click="goPage('/admin/statistics/downStatistics')">
                                다운로드 현황  <span>{{label}}</span>
                            </div>
                            <div class="graph">
                                <v-progress-circular v-if="!chartShow"
                                                     :size="100"
                                                     class="progress-box"
                                                     color="#DD1B16"
                                                     indeterminate
                                ></v-progress-circular>
                                <LineChartGenerator v-if="chartShow"
                                                    :chart-options="chartOptions"
                                                    :chart-data="downChartData"
                                                    :height="height"
                                />
                            </div>
                        </li>
                        <li class="main-li">
                            <div class="text"   @click="goPage('/admin/statistics/memberStatistics')">
                                사용자 방문 현황  <span>{{label}}</span>
                            </div>
                            <div class="graph">
                                <v-progress-circular v-if="!chartShow"
                                                     :size="100"
                                                     class="progress-box"
                                                     color="#DD1B16"
                                                     indeterminate
                                ></v-progress-circular>
                                <LineChartGenerator v-else
                                                    :chart-options="chartOptions"
                                                    :chart-data="chartData"
                                                    :height="height"
                                />
                            </div>
                        </li>
                    </ul>
                </div>
            </div>

        </div>
    </div>
</template>


<script>
    import {Doughnut, Line as LineChartGenerator} from 'vue-chartjs/legacy'

    import {
        Chart as ChartJS,
        Title,
        Tooltip,
        Legend,
        LinearScale,
        CategoryScale,
        ArcElement,
        LineElement,
        PointElement
    } from 'chart.js'

    ChartJS.register(
        Title,
        Tooltip,
        Legend,
        LinearScale,
        CategoryScale,
        ArcElement,
        LineElement,
        PointElement
    )

    import apiService from '@/config/admin/apiService'
    import {
        API_ADMIN_GET_MAIN_INFO
    } from '@/config/admin/urls'
    import UTIL from '@/common/util'

    export default {
        components: {
            LineChartGenerator,
            Doughnut
        },
        data() {
            return {
                height: 300,
                chartData: {
                    labels: [],
                    datasets: []
                },
                donutChartData: {
                    labels: [],
                    datasets: []
                },
                downChartData: {
                    labels: [],
                    datasets: []
                },
                chartOptions: {
                    responsive: true,
                    maintainAspectRatio: false
                },
                chartShow: false,
                busi: 0,
                indi: 0,
                articleTotal: 0,
                keywordData: {
                    name: '',
                    hit: '',
                },
                down: 0,
                label:'',
                stdate: '',
                endate: '',
                dayList: [],
                colorList: [
                    '#C62828',
                    '#4A148C',
                    '#3949AB',
                    '#1E88E5',
                    '#00ACC1',
                    '#00897B',
                    '#43A047',
                    '#827717',
                    '#FFB300',
                    '#F4511E',
                ],
                searchBids: {
                    free: {
                        name: '자유게시판',
                        color: '#41B883'
                    },
                    notice: {
                        name: '공지사항',
                        color: '#FFB300'
                    },
                    ues: {
                        name: '하모니카 사용기',
                        color: '#00D8FF'
                    },
                    qna: {
                        name: '질문과답변',
                        color: '#DD1B16'
                    }
                },
            };
        },

        created() {
            this.setDate();
        },
        methods: {
            setDate() {
                const nowDate = new Date();
                let mon = nowDate.getMonth() + 1;
                let day = nowDate.getDate();
                this.endate = nowDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);
                let endates = this.endate.split('-');

                const stDate = new Date(endates[0], endates[1] - 1, endates[2] - 10);
                mon = stDate.getMonth() + 1;
                day = stDate.getDate();
                this.stdate = stDate.getFullYear() + '-' + ((mon < 10) ? '0' + mon : mon) + '-' + ((day < 10) ? '0' + day : day);

                let stdates = this.stdate.split('-');
                this.dayList = []
                let stYear = stDate.getFullYear();
                let enYear = nowDate.getFullYear();
                let stMonth = stDate.getMonth() + 1;
                let enMonth = nowDate.getMonth() + 1;
                let checkMonthFlag = false;
                let lastDayDate = new Date(stdates[0], stdates[1], 0);
                let lastDay = lastDayDate.getDate();
                let startDay = stDate.getDate();
                let endDay = nowDate.getDate();
                if (stMonth != enMonth) {
                    for (let k = startDay; k <= lastDay; k++) {
                        let date = (k < 10) ? '0' + k : k;
                        let dateMonth = (stMonth < 10) ? '0' + stMonth : stMonth;

                        this.dayList.push({
                            keyDate: dateMonth + '/' + date,
                            viewDate: stYear + '.' + dateMonth + '.' + date,
                        })
                    }
                    startDay = 1;
                }
                for (let k = startDay; k <= endDay; k++) {
                    let date = (k < 10) ? '0' + k : k;
                    let dateMonth = (enMonth < 10) ? '0' + enMonth : enMonth;
                    this.dayList.push({
                        keyDate: dateMonth + '/' + date,
                    })

                }
                this.getData();
            },
            getData() {
                const frm = {
                    stdate: this.stdate.toString(),
                    endate: this.endate.toString()
                }
                this.label = '['+this.stdate+' ~ '+this.endate+']';

                apiService.post(API_ADMIN_GET_MAIN_INFO, frm, (res) => {
                    this.busi = UTIL.numberFormat(res.busi);
                    this.indi = UTIL.numberFormat(res.indi);
                    this.articleTotal = UTIL.numberFormat(res.articleTotal);
                    this.down = UTIL.numberFormat(res.down);
                    if (res.keywordData[0]) {
                        this.keywordData = {
                            name: res.keywordData[0].name,
                            hit: UTIL.numberFormat(res.keywordData[0].hits),
                        }
                    }
                    this.setGraph(res)
                    this.setDownGraph(res);
                    this.setContentGraph(res);
                    this.chartShow = true;
                })
            },
            setGraph(res) {
                this.chartData.labels = [];
                const dataKeyset = {};
                let indexDateKey = ''
                for (const item of res.memberData) {
                    indexDateKey = this.getIndexDateKey(item)
                    dataKeyset[indexDateKey] = item;
                }
                let dataList = [];
                let indiDataList = [];
                let busiDataList = [];
                let nouserDataList = [];

                let index = 1;
                let keyDate = '';
                let dateText = '';
                for (const item of this.dayList) {
                    keyDate = item.keyDate;
                    this.chartData.labels.push(item.keyDate);
                    if (dataKeyset[keyDate]) {
                        dataList.push(dataKeyset[keyDate].total)
                        if (dataKeyset[keyDate].indi) indiDataList.push(dataKeyset[keyDate].indi)
                        else indiDataList.push(0)
                        if (dataKeyset[keyDate].busi) busiDataList.push(dataKeyset[keyDate].busi)
                        else busiDataList.push(0)
                        if (dataKeyset[keyDate].nouser) nouserDataList.push(dataKeyset[keyDate].nouser)
                        else nouserDataList.push(0)

                    } else {
                        dataList.push(0)
                        indiDataList.push(0)
                        busiDataList.push(0)
                        nouserDataList.push(0)

                    }
                    index++;
                }
                this.chartData.datasets = [
                    {
                        label: '전체',
                        backgroundColor: '#DD1B16',
                        data: dataList
                    },
                    {
                        label: '기업회원',
                        backgroundColor: '#00D8FF',
                        data: busiDataList
                    },
                    {
                        label: '개인회원',
                        backgroundColor: '#FFB300',
                        data: indiDataList
                    },
                    {
                        label: '미로그인',
                        backgroundColor: '#41B883',
                        data: nouserDataList
                    },

                ];

            },
            setDownGraph(res) {
                const fileKeyDatas = {};
                this.downChartData.labels = [];
                let fileList = [];
                let k = 0;
                for (const key in res.downData) {
                    if (k > 2) break;
                    let fileName = '';
                    for (let item of res.downData[key]) {
                        let indexDateKey = this.getIndexDateKey(item)
                        if (!fileKeyDatas[key]) fileKeyDatas[key] = {}
                        if (!fileKeyDatas[key][indexDateKey]) fileKeyDatas[key][indexDateKey] = [];
                        fileKeyDatas[key][indexDateKey].push(item)
                        fileName = item['org_file_name'];
                    }
                    fileList.push(fileName)
                    k++;
                }
                let dataList = {};
                let dateIndexKey = '';
                for (const item of this.dayList) {
                    let dayKey = item.keyDate;
                    this.downChartData.labels.push(dayKey);
                    dateIndexKey = dayKey;
                    for (const key in fileKeyDatas) {
                        if (!dataList[key]) dataList[key] = [];
                        if (fileKeyDatas[key][dateIndexKey]) {
                            for (const item of fileKeyDatas[key][dateIndexKey]) {
                                dataList[key].push(item.total)
                            }
                        } else {
                            dataList[key].push(0)
                        }
                    }
                }
                this.downChartData.datasets = [];
                let data = {};
                let index = 0;
                for (const key in fileKeyDatas) {
                    data = {
                        label: fileList[index],
                        backgroundColor: this.colorList[index],
                        data: dataList[key]
                    }
                    this.downChartData.datasets.push(data);
                    index++;
                }

            },
            setContentGraph(res) {
                let total = 0;
                for (const key in res.articleData) {
                    let comment = res.articleData[key].comment;
                    let article = res.articleData[key].article;
                    total = total + (comment + article)
                }

                this.donutChartData.labels = [];
                const articleDonutDatas = [];
                const colors = [];
                for (const key in res.articleData) {
                    this.donutChartData.labels.push(this.searchBids[key].name);
                    colors.push(this.searchBids[key].color)
                    let comment = res.articleData[key].comment;
                    let article = res.articleData[key].article;
                    let itmeTotal = (comment + article)

                    let cnt = 0;
                    if (total > 0) {
                        cnt = parseInt((itmeTotal / total) * 100);
                    }
                    articleDonutDatas.push(cnt)
                }

                this.donutChartData.datasets = [
                    {
                        backgroundColor: colors,
                        data: articleDonutDatas
                    }
                ];
            },
            getIndexDateKey(item) {
                let checkDate = item.date.split('-')
                return checkDate[1] + '/' + checkDate[2];

            },
            locaionHref(url) {

                window.open(url)
            },
            goPage(url) {
                this.$router.push(url)
                this.$eventBus.$emit('goPage', {url: url})
            }

        }

    }
</script>

